import comSwiper from '../../components/comSwiper/comSwiper.vue'
export default{
	data(){
		return{
			
			current:0,
			scrollH:700,
			leftData:[],	
			rightData:[]
		}
	},
	components:{
		comSwiper
	},
	methods:{
		clickCurrent(index){
			this.current = index
		},
		setCurrent(e){
			this.current = e.detail.current
		},
	},
	mounted() {
	},
	created() {
		uni.request({
			url:"http://192.168.1.216:8000/text.json",
			success:(res)=>{
					this.leftData = res.data.leftData;
					this.rightData = res.data.rightData;
					console.log(res)
			},
			fail:(res)=>{
				console.log(res)
			}
		})
		
		
		
		
		uni.getSystemInfo({
			success:(res) =>{
				this.scrollH = res.windowHeight 
			}
		})
	}
}
